/* App.css */
.App {
  font-family: "Arial", sans-serif;
  max-width: 100svw;
  /* margin: 0; */
  margin-top: -1rem;
  margin-inline: -0.99rem;
}

/* Common styles */
button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  margin: 5px;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

.ModalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* Ensure it's above other content */
}

/* Add media queries for responsive design */
@media (max-width: 768px) {
  .App {
    padding: 10px;
  }

  button {
    padding: 8px 16px;
  }
}

.ModalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* High z-index to ensure modals are on top of other content */
}
