.Header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 30px;
  background-color: #d4d1d1;
  border-bottom: 1px solid #ddd;
}

.Header h1 {
  margin: 0;
  color: #333;
  font-size: 24px;
  padding-left: 20px;
}

.Header a {
  text-decoration: none;
}

.Header nav {
  display: flex;
  align-items: center;
}
.Header div span {
  margin-right: 10px;
  color: #333;
  font-size: 18px;
}

.Header .CartIcon {
  margin-left: 20px;
  padding-right: 20px;
}

.CartIcon img {
  height: 26px;
  width: auto;
  cursor: pointer;
  margin-right: 1rem;
}
.ProfileIcon img {
  height: 30px;
  width: auto;
}
.Header button {
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1;
  border-radius: 4px;
}

/* Responsive styles */
@media (max-width: 768px) {
  .Header {
    flex-direction: column;
    align-items: flex-start;
  }

  .Header h1,
  .Header div,
  .Header .CartIcon {
    width: 100%;
    justify-content: space-between;
    padding: 10px 0;
  }

  .Header div {
    order: 2;
  }

  .Header .CartIcon {
    order: 3;
  }

  .Header div span {
    font-size: 16px;
  }
}
